import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LayoutComponent } from './pages/layout/layout.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { RegisterComponent } from './pages/register/register.component';

const routes: Routes = [
    {
        path: '',
        component: LayoutComponent,
        children: [
            {
                path: '',
                loadChildren: () => import('./pages/home/home.module').then(m => m.HomeModule),
                pathMatch: 'full'
            }, {
                path: 'register',
                component: RegisterComponent,
                children: [
                    {
                        path: '',
                        loadChildren: () => import('./pages/register/step1/step1.module').then(m => m.Step1Module),
                        pathMatch: 'full'
                    }, {
                        path: 'step2',
                        loadChildren: () => import('./pages/register/step2/step2.module').then(m => m.Step2Module)
                    }, {
                        path: 'step3',
                        loadChildren: () => import('./pages/register/step3/step3.module').then(m => m.Step3Module)
                    }, {
                        path: 'login',
                        loadChildren: () => import('./pages/register/login/login.module').then(m => m.LoginModule)
                    }
                ]
            }
        ]
    }, {
        path: 'dashboard',
        component: DashboardComponent,
        children: [
            {
                path: '',
                loadChildren: () => import('./dashboard/happy-home/happy-home.module').then(m => m.HappyHomeModule),
                pathMatch: 'full'
            }, {
                path: 'happy-history',
                loadChildren: () => import('./dashboard/happy-history/happy-home.module').then(m => m.HappyHistoryModule)
            }, {
                path: 'happy-trading',
                loadChildren: () => import('./dashboard/happy-trading/happy-trading.module').then(m => m.HappyTradingModule)
            }, {
                path: 'happy-settings',
                loadChildren: () => import('./dashboard/happy-settings/happy-settings.module').then(m => m.HappySettingsModule)
            }
        ]
    }, {
        path: '**',
        component: PageNotFoundComponent
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, {
        initialNavigation: 'enabled',
        scrollPositionRestoration: 'enabled',
        anchorScrolling: 'enabled',
        relativeLinkResolution: 'legacy'
    })],
    exports: [RouterModule]
})
export class AppRoutingModule { }
